import { Injectable } from '@angular/core';
import { Http, Headers, Response, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import { ProductService } from '../service/product.service';

@Injectable()
export class StorageService {

  public departaments = [];

  constructor(private http: Http, private produto: ProductService) { }


  public obterStorage(nome, id = ''): Observable<any> {

      switch (nome) {
        case 'menu-departamento':
          return this.produto.getCategory();

        case 'sub-departamento':
          return this.produto.getSubCategory(id);

        case 'banner-principal':
          // TODO
          break;

        case 'banner-grid':
          // TODO
          break;

        default:
          break;
      }
  }

  getCacheVersion(): Observable<any> {
    return this.http.get('http://localhost:3000/cache-version');
  }

  public managerCache() {

    this.getCacheVersion().subscribe(
      cache => {
        this.checkCache(cache.json());
      }
    );
  }

  updateCache(name, version) {

    // this.obterStorage(name).subscribe(
    //   data => {
    //     const newJson = { 'version': version, data };
    //     localStorage.setItem(name, JSON.stringify(newJson));
    //     return data;
    //   });

    debugger;
      this.obterStorage('menu-departamento').subscribe(
        data => {

          if (data !== undefined) {
            data.forEach(category => {
              this.obterStorage('sub-departamento', category.uniqueID).subscribe(sub => {
                category.sub = sub;
                this.departaments.push(category);

                const dataStorage = { 'version': version, data };
                localStorage.setItem(name, JSON.stringify(dataStorage));

                // localStorage.setItem('menu-departamento', JSON.stringify(this.departaments));

              });

            });
          }
        },
      );
  }

  checkCache(lastVersion) {

    const menuDepartamento = JSON.parse(localStorage.getItem('menu-departamento'));
    const bannerPrincipal = JSON.parse(localStorage.getItem('banner-principal'));
    const bannerGrid = JSON.parse(localStorage.getItem('banner-grid'));

    if ((menuDepartamento == null) || (lastVersion['menu-departamento'] !== menuDepartamento['version'])) {
      this.updateCache('menu-departamento', lastVersion['menu-departamento']);
    }

  }

}



