import { Injectable } from '@angular/core';
import { Http, Headers, Response, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import { Jsonp } from '@angular/http';

@Injectable()
export class AuthenticationService {

  public WCToken: string;
  public WCTrustedToken: string;

  private url = 'https://www.fastshop.com.br/wcs/resources/';

  constructor(private http: Http) { }

  login(document: string, password: string): Observable<any> {
    return this.http.post(this.url + 'v1/auth/login', {document: document, password: password})
      .map((response: Response) => {
        this.WCToken = response.json().WCToken;
        this.WCTrustedToken = response.json().WCTrustedToken;

        if (this.WCToken && this.WCTrustedToken) {
          localStorage.setItem('currentUser', JSON.stringify({ WCToken: this.WCToken, WCTrustedToken: this.WCTrustedToken }));
          return true;
        } else {
          return false;
        }
      });
  }

}



