import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { SpotService } from '../../service/spot.service';
import { StorageService } from '../../service/storage.service';
import { ProductService } from '../../service/product.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  @ViewChild('dataContainer') dataContainer: ElementRef;

  private itens;
  private kitchen;
  private livingRoom;
  public images;

  constructor(private spot: SpotService, private storage: StorageService, private product: ProductService) { }

  ngOnInit() {
    this.loadSpot();
    this.loadItens();
    this.storage.managerCache();
  }

  private loadSpot() {

    this.spot.getSpot('Home_Topo_01_cont')
      .subscribe(
      data => {
        const inicio = data._body.indexOf('var ObjData = ');
        const fim = data._body.indexOf('"type": "json"');
        const result = data._body.substring(inicio + 13, fim - 9) + ']}';
        const obj = JSON.parse(result);

        const img = [];
        obj.carousel.forEach(d => {
          img.push({ image: obj.pathname + d.image, link: d.link });
        });

        this.images = img;
      }
      );
  }

  private loadItens() {
    this.product.getProductByCategoryId(22004, 0).subscribe(
      data => {
        // this.itens = data.products;
        this.itens = this.range(data.products, 8);
        // debugger;
        // const x = this.srange(0, 3);
      }
    );

    this.product.getProductByCategoryId(22002, 0).subscribe(
      data => {
        this.kitchen = this.range(data.products, 8 );
      }
    );

    this.product.getProductByCategoryId(22008, 0).subscribe(
      data => {
        this.livingRoom = this.range(data.products, 8 );
      }
    );

  }

  // srange(start, count) {
  //   return Array.apply(0, Array(count))
  //     .map(function (element, index) {
  //       return index + start;
  //     });
  // }


  range(list, last) {
    const newList = [];

    list.forEach(function (value, i) {
      if ( i >= last) {
        return true;
      }
      newList.push(value);
      console.log('%d: %s', i, value);
    });

    return newList;
  }

}



